import React, { useState } from "react";
import "./App.css";
// components
import Header from "./components/Header";
import MainBox from "./components/MainBox";
import Projects from "./components/Projects";
import ContactBox from "./components/ContactBox";
import Rightbox from "./components/RightBox";

function App() {
  const [hover, setCircle] = useState({
    url: "https://i.imgur.com/qhRmXhk.jpg",
  });

function changeCircle(url) {
  setCircle({
    ...hover, 
    url})
}

  return (
    <div>
      <Header />
      <div id="main-container">
        <div id="left">
          <div
            onMouseEnter={() => {
              setCircle({ url: "https://i.imgur.com/qhRmXhk.jpg" });
            }}
          >
            <MainBox />
          </div>

      
            <Projects changeCircle={changeCircle} />
          
          <ContactBox />
        </div>

        <div id="right">
          <Rightbox props={hover} />
        </div>
      </div>
      <div className="wave-container">
<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
  <path fill="#a2d9ff" fill-opacity="0.6" d="M0,160L48,181.3C96,203,192,245,288,229.3C384,213,480,139,576,117.3C672,96,768,128,864,138.7C960,149,1056,139,1152,122.7C1248,107,1344,85,1392,74.7L1440,64L1440,320L1392,320C1344,320,1248,320,1152,320C1056,320,960,320,864,320C768,320,672,320,576,320C480,320,384,320,288,320C192,320,96,320,48,320L0,320Z"></path>
</svg>
</div>
</div>

  );
}

export default App;
